.wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  min-height: 20rem;
  padding: 2rem;
  background-color: lighten(#61082b, 5);
  text-align: center;
  border-radius: 2rem;
}

.title {
  color: white;
  margin: 1rem 0;
  font-size: 5rem;
}

.message {
  font-size: 2.8rem;
  color: white;
}

.instruction {
    margin-top: 4rem;
  font-size: 1.8rem;
  color: wheat;
}
